@import "../../core/style/mixins";
@import "../../core/style/variables";
@import "../../core/style/shadows";
@import "../../core/style/default-theme";

$tabs-paginator-width: $baseline-grid * 4 !default;
$tabs-tab-width: $baseline-grid * 12 !default;
$tabs-header-height: 48px !default;

@keyframes md-tab-content-hide {
  0% {
    opacity: 1;
  }
  50% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

md-tab-data {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  z-index: -1;
  opacity: 0;
}

md-tabs {
  display: block;
  margin: 0;
  border-radius: 2px;
  overflow: hidden;
  position: relative;
  flex-shrink: 0;
  &:not(.md-no-tab-content):not([md-dynamic-height]) {
    min-height: 200 + $tabs-header-height;
  }
  &[md-align-tabs="bottom"] {
    padding-bottom: $tabs-header-height;
    md-tabs-wrapper {
      position: absolute;
      bottom: 0;
      left: 0;
      right: 0;
      height: $tabs-header-height;
      z-index: 2;
    }
    md-tabs-content-wrapper {
      top: 0;
      bottom: $tabs-header-height;
    }
  }
  &[md-dynamic-height] {
    md-tabs-content-wrapper {
      min-height: 0;
      position: relative;
      top: auto;
      left: auto;
      right: auto;
      bottom: auto;
      overflow: visible;
    }
    md-tab-content {
      &.md-active {
        position: relative;
      }
    }
  }
  &[md-border-bottom] {
    md-tabs-wrapper {
      border-width: 0 0 1px;
      border-style: solid;
    }
    &:not([md-dynamic-height]) {
      md-tabs-content-wrapper {
        top: $tabs-header-height + 1;
      }
    }
  }
}

md-tabs-wrapper {
  display: block;
  position: relative;
  md-prev-button, md-next-button {
    height: 100%;
    width: $tabs-paginator-width;
    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    line-height: 1em;
    z-index: 2;
    cursor: pointer;
    font-size: 16px;
    background: transparent no-repeat center center;
    transition: $swift-ease-in-out;
    &:focus {
      outline: none;
    }
    &.md-disabled {
      opacity: 0.25;
      cursor: default;
    }
    &.ng-leave {
      transition: none;
    }
    [md-icon] {
      position: absolute;
      top: 50%;
      left: 50%;
      transform: translate3d(-50%, -50%, 0);
    }
  }
  md-prev-button {
    left: 0;
    background-image: url('data:image/svg+xml;base64,');
  }
  md-next-button {
    right: 0;
    background-image: url('data:image/svg+xml;base64,');
    [md-icon] {
      transform: translate3d(-50%, -50%, 0) rotate(180deg);
    }
  }
  &.md-stretch-tabs {
    md-pagination-wrapper {
      width: 100%;
      display: flex;
      flex-direction: row;
      md-tab-item {
        flex-grow: 1;
      }
    }
  }
}

md-tabs-canvas {
  @include pie-clearfix;
  position: relative;
  overflow: hidden;
  display: block;
  height: $tabs-header-height;
  .md-dummy-wrapper {
    position: absolute;
    top: 0;
    left: 0;
  }
  &.md-paginated {
    margin: 0 $tabs-paginator-width;
  }
  &.md-center-tabs {
    display: flex;
    flex-direction: column;
    text-align: center;
    .md-tab {
      float: none;
      display: inline-block;
    }
  }
}

md-pagination-wrapper {
  @include pie-clearfix;
  height: $tabs-header-height;
  display: block;
  transition: transform $swift-ease-in-out-duration $swift-ease-in-out-timing-function;
  position: absolute;
  width: 999999px;
  left: 0;
  transform: translate3d(0, 0, 0);
  &.md-center-tabs {
    position: relative;
    width: initial;
    margin: 0 auto;
  }
}

md-tabs-content-wrapper {
  display: block;
  position: absolute;
  top: $tabs-header-height;
  left: 0;
  right: 0;
  bottom: 0;
  overflow: hidden;
}

md-tab-content {
  display: block;
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  transition: transform $swift-ease-in-out-duration $swift-ease-in-out-timing-function;
  overflow: auto;
  &.md-no-scroll {
    bottom: auto;
    overflow: hidden;
  }
  &.ng-leave, &.md-no-transition {
    transition: none;
  }
  &.md-left {
    transform: translateX(-100%);
    animation: 2 * $swift-ease-in-out-duration md-tab-content-hide;
    opacity: 0;
    * {
      transition: visibility 0s linear;
      transition-delay: $swift-ease-in-out-duration;
      visibility: hidden;
    }
  }
  &.md-right {
    transform: translateX(100%);
    animation: 2 * $swift-ease-in-out-duration md-tab-content-hide;
    opacity: 0;
    * {
      transition: visibility 0s linear;
      transition-delay: $swift-ease-in-out-duration;
      visibility: hidden;
    }
  }
  > div.ng-leave {
    animation: 2 * $swift-ease-in-out-duration md-tab-content-hide;
  }
}

md-ink-bar {
  $duration: $swift-ease-in-out-duration * 0.5;
  $multiplier: 0.5;
  position: absolute;
  left: auto;
  right: auto;
  bottom: 0;
  height: 2px;
  &.md-left {
    transition: left ($duration * $multiplier) $swift-ease-in-out-timing-function,
    right $duration $swift-ease-in-out-timing-function;
  }
  &.md-right {
    transition: left $duration $swift-ease-in-out-timing-function,
    right ($duration * $multiplier) $swift-ease-in-out-timing-function;
  }
}

[md-tab] {
  position: absolute;
  z-index: -1;
  left: -9999px;
}

.md-tab {
  font-size: 14px;
  text-align: center;
  line-height: $tabs-header-height - 24;
  padding: 12px 24px;
  transition: background-color 0.35s $swift-ease-in-out-timing-function;
  cursor: pointer;
  white-space: nowrap;
  position: relative;
  text-transform: uppercase;
  float: left;
  font-weight: 500;
  box-sizing: border-box;
  overflow: hidden;
  text-overflow: ellipsis;
  &.md-focused {
    box-shadow: none;
    outline: none;
  }
  &.md-active {
    cursor: default;
  }
  &.md-disabled {
    pointer-events: none;
    touch-action: pan-y;
    user-select: none;
    -webkit-user-drag: none;
    opacity: 0.5;
    cursor: default;
  }
  &.ng-leave {
    transition: none;
  }
}

md-toolbar + md-tabs {
  border-top-left-radius: 0;
  border-top-right-radius: 0;
}

//
// Theme
//
@mixin md-tab-primary {
  > md-tabs-wrapper {
    background-color: md-color($md-primary); //'{{primary-color}}';
    > md-tabs-canvas {
      > md-pagination-wrapper {
        > md-tab-item:not([disabled]) {
          color: md-color($md-primary, lighter); // '{{primary-100}}';
          &.md-active, &.md-focused {
            &, [md-icon] {
              color: md-color($md-primary, default-contrast); // '{{primary-contrast}}';
            }
          }
          &.md-focused {
            background: md-color($md-primary, lighter-contrast); // '{{primary-contrast-0.1}}';
          }
        }
      }
    }
  }
}

@mixin md-tab-warn {
  > md-tabs-wrapper {
    background-color: md-color($md-warn); //'{{warn-color}}';
    > md-tabs-canvas {
      > md-pagination-wrapper {
        > md-tab-item:not([disabled]) {
          color: md-color($md-warn, lighter); // '{{warn-100}}';
          &.md-active, &.md-focused {
            &, [md-icon] {
              color: md-color($md-warn, default-contrast); // '{{warn-contrast}}';
            }
          }
          &.md-focused {
            background: md-color($md-warn, lighter-contrast); // '{{warn-contrast-0.1}}';
          }
        }
      }
    }
  }
}

@mixin md-tab-accent {
  > md-tabs-wrapper {
    background-color: md-color($md-accent); //'{{accent-color}}';
    > md-tabs-canvas {
      > md-pagination-wrapper {
        > md-tab-item:not([disabled]) {
          color: md-color($md-accent, lighter); // '{{accent-100}}';
          &.md-active, &.md-focused {
            &, [md-icon] {
              color: md-color($md-accent, default-contrast); // '{{accent-contrast}}';
            }
          }
          &.md-focused {
            background: md-color($md-accent, lighter-contrast); // '{{accent-contrast-0.1}}';
          }
        }
        > md-ink-bar {
          color: md-color($md-primary, 600); // '{{primary-600-1}}';
          background: md-color($md-primary, 600); // '{{primary-600-1}}';
        }
      }
    }
  }

}

// Default theme
md-tabs {
  md-tabs-wrapper {
    background-color: transparent;
    border-color: md-color($md-foreground, divider); // '{{foreground-4}}';
  }
  .md-paginator [md-icon] {
    color: md-color($md-primary); // '{{primary-color}}';
  }

  md-ink-bar {
    color: md-color($md-accent); // '{{accent-color}}';
    background: md-color($md-accent); // '{{accent-color}}';
  }

  .md-tab {
    color: md-color($md-foreground, text); // '{{foreground-2}}';
    &[disabled] {
      &, [md-icon] {
        color: md-color($md-foreground, disabled-text); // '{{foreground-3}}';
      }
    }
    &.md-active, &.md-focused {
      &, [md-icon] {
        color: md-color($md-primary); // '{{primary-color}}';
      }
    }
    &.md-focused {
      background: md-color($md-primary, lighter); // '{{primary-color-0.1}}';
    }
    .md-ripple-container {
      color: md-color($md-accent, 100); // '{{accent-100}}';
    }
  }

  &.md-accent {
    @include md-tab-accent();
  }

  &.md-primary {
    @include md-tab-primary();
  }

  &.md-warn {
    @include md-tab-warn();
  }
}

md-toolbar > md-tabs {
  @include md-tab-primary();
}

md-toolbar.md-accent > md-tabs {
  @include md-tab-accent();
}

md-toolbar.md-warn > md-tabs {
  @include md-tab-warn();
}

